import React, { PureComponent } from "react";
import ReactHtmlParser from 'react-html-parser';
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import { GET_MEDIA_NAME } from "../../../../../Services/Utils/utils";

export default class PopUpAudio extends PureComponent {
    constructor(props) {
        super(props);
    }
    onErrorImageUpdate = event => {
        event.target.src = baseUrl(`media/images/error/image.svg`)
    }
    render() {
        const { data } = this.props;
        return <div className="w-100" style={{maxWidth:420}}>
            <img className="img-fluid w-100" loading="lazy" src={data.picture} height={260} width={260} alt={GET_MEDIA_NAME()} onError={this.onErrorImageUpdate} />
            {ReactHtmlParser(data.mediaFile)}
        </div>
        
    }
}